.terms {
    @extend %size_screen;
    color: #333;

    p, ul, li {
        text-align: justify;
        font-size: 15px;
    }

    h3 {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        background-color: #eee;
        padding: 10px;
        border-radius: 3px;
        color: darken(#333, 5%);
        color: #333;

        a i { color: darken(#333, 5%); }
    }

    &_definitions,
    &_user { margin-top: 20px; }

    ul {
        list-style: unset;
        margin-left: 18px;

        li:not(:first-child) { margin-top: 12px; }
        li span {
            font-weight: 700;
            color: darken(#333, 5%);
        }
    }
}
